/**
 * @author fanhualing 2018-4-18 新增联系策略
 */
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service'
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD, TZB_CUS_UPLOAD2 } from 'app/@uisftech/common/service/http.constant';
import { ImportCustomerBean, AddPolicyBean, CustomerSearchBean } from "app/pages/tzb/custom/custom-marketing/custom-contact/policy-settings/add-policy/bean/add-policy.bean";
import { ActivatedRoute, Router } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CommonHttpService } from "../../../../../common/http/common.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'app-add-policy',
  templateUrl: './add-policy.component.html',
  styleUrls: ['./add-policy.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class AddPolicyComponent implements OnInit {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';

  importCustomerBean: ImportCustomerBean = new ImportCustomerBean();
  addPolicyBean: AddPolicyBean = new AddPolicyBean();
  customerSearchBean: CustomerSearchBean = new CustomerSearchBean();
  code: any;
  contactTheme: any;
  custSource: any;
  contactWay: any;
  status: any = [];
  receiveType: any = [];
  contactOrg: any = [];
  userform: FormGroup;
  user: any;
  userName: string = '';
  orgName: string = '';
  isFirstStep: boolean = true;
  isImport: boolean = true;
  postInfoList: any;
  postList: any;
  msgs: any = [];
  files: any = [];
  name: string;
  fileList: any = [];
  pageList: any = [];
  totalNum: number;
  importTrue: boolean = false;
  postQueryCondition: any;
  planContactTime: any;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOAD;
  ch: any = CALENDAR_CN;
  contactStrategyId: string;
  otherTheme: boolean = false;
  lenRemark: boolean = false;
  commentsRequire: boolean = false;
  isReset: boolean = false;
  timeError: boolean = false;
  planContactTimeStart: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public routeInfo: ActivatedRoute,
    public router: Router,
    public fb: FormBuilder,
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
  ) {

    //获取码值
    this.codeValues();
    this.contactTheme = this.code['ContactTheme'];
    this.custSource = this.code['CustSource'];
    this.contactWay = this.code['ContactWays'];
    // 校验
    this.userform = fb.group({
      contactTheme: ['', Validators.required],
      contactWay: ['', Validators.required],
      receiveType: ['', Validators.required],
      planContactTime: ['', Validators.required],
      custSource: ['', Validators.required],
      comments: ['', Validators.required],
      remark1: [''],
    })

    this.planContactTimeStart = new Date(this.getMinMaxDate('min'));
    // 查公共接口-获取任务接收类型
    this.queryOrgPeople();
  }

  // 初始化参数
  ngOnInit() {

    // 判断是否从高级搜索页面返回
    if (this.commfunc.getSessionDataCH('cusConditionId')) {
      this.isFirstStep = false;
      this.isImport = false;
      this.customerSearchBean.custConditionId = this.commfunc.getSessionDataCH('cusConditionId');
    }
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    if (this.inValue) {
      if (this.inValue.type == 'advance') {
        this.contactStrategyId = this.inValue.contactStrategyId;
        this.isFirstStep = false;
        this.isImport = false;
        this.link();
      } else {
        this.contactStrategyId = this.inValue.contactStrategyId;
        this.isFirstStep = false;
        this.isImport = true;
      }
    }
  }

  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  // 任务接收类型
  queryOrgPeople() {
    let param = {
      pageNum: 1,
      pageSize: 100000000,
      extParam: 'custContact',
    }
    let ob = this.commonHttpService.queryPostById(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.postInfoList) {
          this.postInfoList = data.postInfoList;
          this.postList = this.commfunc.listToLabelvalue(data.postInfoList, 'postName', 'postId');
          this.postList.unshift({ label: "请选择", value: '' });
          this.postList.push({ label: "主联系人", value: '0' });
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
      this.commfunc.clearSession('cusConditionId')
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.commfunc.clearSession('cusConditionId')
    })
  }

  // 更改主题，当主题为其他时，显示说明字段
  changeTheme() {
    if (this.addPolicyBean.contactTheme == '06') {
      this.otherTheme = true;
    } else {
      this.otherTheme = false;
    }
  }

  // 改变任务接收类型
  receiveTypeChange(e) {
    if (this.postInfoList && this.postInfoList.length > 0) {
      this.postInfoList.map(element => {
        if (e.value == element.postId) {
          this.addPolicyBean.extParam = element.extParam;
        }
      });
    }
  }

  // 新增策略
  goNext() {
    // 重置标志
    this.isReset = false;
    // 校验其他主题-说明字段
    if (this.addPolicyBean.contactTheme == '06') {
      if (this.addPolicyBean.remark1 != undefined && this.addPolicyBean.remark1 != null && this.addPolicyBean.remark1 != '') {
        if (this.addPolicyBean.remark1.length > 10) {
          this.lenRemark = true;
        } else {
          this.lenRemark = false;
        }
      }
    }

    // 校验页面数据
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    // 如果说明长度过长
    if (this.lenRemark == true) {
      return
    }

    // 如果备注长度过长
    if (this.addPolicyBean.comments.length > 200) {
      this.commentsRequire = true;
      return
    } else {
      this.commentsRequire = false;
    }

    // 根据任务接收类型receiveType，查询任务接收类型名称receiveTypeName
    for (let i of this.postList) {
      if (i.value == this.addPolicyBean.receiveType) {
        this.addPolicyBean.receiveTypeName = i.label;
        if (this.postInfoList && this.postInfoList.length > 0) {
          this.postInfoList.map(element => {
            if (i.value == element.postId) {
              this.addPolicyBean.remark2 = element.extParam;
            }
          });
        }
        continue;
      }
    }

    let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.addPolicyBean.userId = user.userId;//创建人id
    this.addPolicyBean.createUserName = ch.userName;//创建人名

    this.addPolicyBean.planContactTime = this.commfunc.transDateN(this.planContactTime);
    this.addPolicyBean.sourceKind = '1';
    let ob = this.httpService.insertCustomerContactStrategy(this.addPolicyBean);
    ob.subscribe(data => {
      if (data.returnCode.code == "success") {
        // 新增策略成功，进入下一步页面
        this.contactStrategyId = data.contactStrategyId;
        this.isFirstStep = false;
        if (this.addPolicyBean.custSource == '1') {
          this.isImport = true;
        } else {
          this.isImport = false;
          this.link();
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }

  // 重置
  doReset() {
    this.addPolicyBean = new AddPolicyBean();
    this.planContactTime = null;
    this.isReset = true; // 重置标志
    this.otherTheme = false; // “说明”这行不显示
    this.commentsRequire = false; //备注
    this.timeError = false;
  }

  // 名单导入
  load(event) {
    for (let file of event.files) {
      this.files = [];
      this.files.push(file)
    }
    // if (event.xhr.response) {
    //   this.fileList = JSON.parse(event.xhr.response)[0];
    // }
    // if (this.fileList) {
    //   if (this.fileList.hasOwnProperty('errorMsg')) {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.returnCode.errorMsg });
    //     this.files = [];
    //   } else {
    //     this.importCustomerBean.sourceFileUrl = this.fileList.fileUrl; // 文件导入-文件名
    //     this.importCustomerBean.sourceFileName = this.fileList.fileName; // 文件导入-文件URL
    //   }
    // }


    if (event.xhr.response) {
      if (JSON.parse(event.xhr.response).success == false) {
        this.files = [];
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
        return
      } else {
        this.fileList = JSON.parse(event.xhr.response)[0];
        this.importCustomerBean.sourceFileUrl = this.fileList.fileUrl; // 文件导入-文件名
        this.importCustomerBean.sourceFileName = this.fileList.fileName; // 文件导入-文件URL
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
      }
    }

  }

  // 名单导入-请确认是否上传该文件?
  submitInport() {
    this.confirmationService.confirm({
      message: '请确认是否上传该文件?',
      accept: () => {
        this.submitNameList();
      },
      reject: () => {
        this.outValue.emit(false);
        this.customerSearchBean.custConditionId = '';
      }
    });
  }
  submitNameList() {
    if (!this.importCustomerBean.sourceFileName) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "名单未上传!" });
      return
    }
    this.importCustomerBean.contactStrategyId = this.contactStrategyId; // 联系策略标识
    let ob = this.httpService.importCustomerContactByFile(this.importCustomerBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //高级搜索
  link() {
    let bean = JSON.stringify(this.addPolicyBean);
    this.commfunc.setSessionDataCH('strategyId', this.contactStrategyId)
    let param = {
      type: 'add', //类型判断是高级搜索
      sessionBean: bean, //页面中输入值
    }
    this.router.navigate(['pages/tzb/custom/custom-information/advanced-search/add-search', param]); //跳转到高级搜索
  }

  // 高级搜索-请确认是否导入该搜索结果?
  breakcast() {
    this.confirmationService.confirm({
      message: '请确认是否导入该搜索结果?',
      accept: () => {
        this.submit();
      },
      reject: () => {
        this.outValue.emit(false);
        this.customerSearchBean.custConditionId = '';
      }
    });
  }
  submit() {
    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.commfunc.getSessionDataCH('strategyId')) {
      this.customerSearchBean.contactStrategyId = this.commfunc.getSessionDataCH('strategyId');
    }
    let ob = this.httpService.importCustomerContactByHighQuery(this.customerSearchBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(false);
        this.customerSearchBean.custConditionId = ''
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
      this.commfunc.clearSession('cusConditionId');
      this.commfunc.clearSession('strategyId');
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.commfunc.clearSession('cusConditionId');
      this.commfunc.clearSession('strategyId');
    })
  }

  // 手动输入日期
  handInputDate(param) {
    let date = new Date();
    if (param == 'planContactTime') {
      if (this.planContactTime != null && this.planContactTime != '') {
        if (this.commfunc.transDateN(this.planContactTime) < this.commfunc.transDateN(date)) {
          this.planContactTime = null;
          this.timeError = true;
        }
      } else {
        this.timeError = false;
      }
    }
  }

  // 获取今天最大最小日期
  getMinMaxDate(param) {
    let time = new Date();
    let year = time.getFullYear();
    let month = time.getMonth() + 1 + '';
    let data = time.getDate() + '';
    if (month.length < 2) {
      month = '0' + month;
    }
    if (data.length < 2) {
      data = "0" + data
    }
    let todayTime;
    if (param == 'min') {
      todayTime = year + "/" + month + "/" + data + ' 00:00:00';
    }
    if (param == 'max') {
      todayTime = year + "/" + month + "/" + data + ' 23:59:59';
    }
    return todayTime;
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
