import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service'
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/components/common/api';
import { AddPlanBean } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-plan/add-plan/bean/addPlan.bean";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-add-plan',
  templateUrl: './add-plan.component.html',
  styleUrls: ['./add-plan.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
/**
 * 新建客户联系计划
 */
export class AddPlanComponent implements OnInit {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';

  addPlanBean: AddPlanBean = new AddPlanBean();
  lxzt: any = [];
  contactWay: any = [];
  contactOrg: any = [];
  contactDate: any;
  CALENDAR_CN: any = [];
  msgs: Message[] = [];
  user: any;
  user2: any;
  orgId: string = '';
  userId: string = '';
  userName: string = '';
  roleName: string = '';
  cars: any = [];
  userform: FormGroup;
  custNoTipMess = '';
  custNoTip: boolean = false;
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.lxzt = this.code['Lxzt'];
    this.contactWay = this.code['ContactWays'];
    this.contactOrg = this.code['ContactOrg'];

    this.CALENDAR_CN = {
      "firstDayOfWeek": 0,
      "dayNames": ["周日", "周一", "周二", "周三", "周四", "周五", "周六"],
      "dayNamesShort": ["星期天", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"],
      "dayNamesMin": ["日", "一", "二", "三", "四", "五", "六"],
      "monthNames": ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"],
      "monthNamesShort": ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"]
    }
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user.orgId) {
      this.addPlanBean.contactOrgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.addPlanBean.contactsNo = this.user.userId;
    }
    if (this.user2.userName) {
      this.addPlanBean.contactsName = this.user2.userName;
    }
    if (this.user2.orgName) {
      this.addPlanBean.contactOrg = this.user2.orgName;
    }

    this.userform = fb.group({
      custNo: ['', Validators.required],
      custName: ['', Validators.required],
      contactTheme: ['', Validators.required],
      contactWay: ['', Validators.required],
      contactDate: ['', Validators.required],
      contactsNo: [''],
      contactsName: [''],
      contactOrg: [''],
      taskRemarks: [''],
    })
  }

  ngOnInit() {
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //提交-保存
  submit() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.cars = [];
    this.cars.push(this.addPlanBean);
    let param = {
      custContactList: this.cars
    };

    let ob1 = this.httpService.checkCustContactInfo(
      { custNo: this.addPlanBean.custNo, interfaceNo: '1', contactTheme: this.addPlanBean.contactTheme }
    );
    ob1.subscribe(data => {
      if (data.returnCode.code == 'success') {
        let ob = this.httpService.insertCustContactProgram(param);
        ob.subscribe(data => {
          if (data.returnCode.code == 'success') {
            // this.msgs = [];
            // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.outValue.emit(false);
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //关闭
  close() {
    this.outValue.emit(false);
  }

  //日期转换
  showData() {
    this.addPlanBean.contactDate = this.commfunc.transDate(this.contactDate);
  }

  //回显客户名称
  showCustName() {
    this.custNoTip = false;
    let param = { custNo: this.addPlanBean.custNo, interfaceNo: '0' }
    let ob1 = this.httpService.checkCustContactInfo(param);
    ob1.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.addPlanBean.custName = data.custName;
      } else {
        if (this.addPlanBean.custNo != '') {
          this.custNoTip = true;
          this.custNoTipMess = data.returnCode.message
        }
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

}
