import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomerContactComponent}from './customer-contact.component';
import { PolicySettingsComponent } from './policy-settings/policy-settings.component';
import { ContactTrackComponent } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-track/contact-track.component";
import { ContactTrackSummaryComponent } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-track-summary/contact-track-summary.component";
import { DealTaskComponent } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-track/deal-task/deal-task.component";
import { CustomContactPolicyComponent } from "app/pages/tzb/custom/custom-marketing/custom-contact/custom-contact-policy/custom-contact-policy.component";
import { ContactListComponent } from './contact-list/contact-list.component';

export const routes: Routes = [
    
    {
        path: '',
        component: CustomerContactComponent,
        children: [
            { path: '', redirectTo: 'custom-contact-policy',pathMatch: 'full' },
            { path: 'custom-contact-policy', component: CustomContactPolicyComponent, 
            children: [
                { path: '', redirectTo: 'policy-settings', pathMatch: 'full' },
                {
                    path: 'policy-settings', component: PolicySettingsComponent,
                },
                {
                    path: 'contact-progress-track', component: ContactTrackComponent,
                },
                {
                    path: 'contact-track-summary', component: ContactTrackSummaryComponent,
                },
                {
                    path: 'contact-list', component: ContactListComponent,
                },
                ],
            },
        ]
    }

]

export const CusContactRouting: ModuleWithProviders = RouterModule.forChild(routes);
