import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { PolicyPageBean } from './bean/policy-exam.bean';

@Component({
  selector: 'app-policy-exam',
  templateUrl: './policy-exam.component.html',
  styleUrls: ['./policy-exam.component.scss'],
  providers: [CusOperationHttpService]
})
export class PolicyExamComponent implements OnInit {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';

  ch: any = CALENDAR_CN;
  msgs: any = [];
  policyPageBean: PolicyPageBean = new PolicyPageBean();
  contactTheme: any;
  contactWay: any;
  custSource: any;
  receiveTypeName: any;
  receiveType: any;
  createdUser: any;
  createdUserName: any;
  count: any;
  code: any;
  theme: any;
  source: any;
  ways: any;

  constructor(
    public httpService: CusOperationHttpService,
    public route: ActivatedRoute,
    public commfunc: Commfunc,
  ) {
  }

  //初始化
  ngOnInit() {
    //获取码值
    this.codeValues();
    this.theme = this.code['ContactTheme'];
    this.source = this.code['CustSource'];
    this.ways = this.code['ContactWays'];

    this.policyPageBean.contactStrategyId = this.route.snapshot.params['contactStrategyId'];
    this.doQuery();
  }

  //审批-详情
  doQuery() {
    let ob = this.httpService.queryCustomerContactStrategyApprove(this.policyPageBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.contactTheme = data.contactTheme;
        this.contactWay = data.contactWay;
        this.custSource = data.custSource;
        this.receiveTypeName = data.receiveTypeName;
        this.receiveType = data.receiveType;
        this.createdUser = data.createdUser;
        this.createdUserName = data.createdUserName;
        this.count = data.count;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }
}