/**
 * @author fanhualing 2018-4-21 客户联系进度跟踪
 */
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MineCustomProBean, CustomerQuery } from "app/pages/tzb/custom/custom-information/mine-custom/mine-custom-overview2/bean/mineCustomPro.bean";
import { CustContactTrackBean } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-track/bean/custContactTrack.bean";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Router, ActivatedRoute } from "@angular/router";
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-contact-track',
  templateUrl: './contact-track.component.html',
  styleUrls: ['./contact-track.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})

export class ContactTrackComponent implements OnInit {
  @Output() outValueBean: EventEmitter<any> = new EventEmitter();

  contactOrg: any = [];
  receiveType: any = [];
  approveStatus: any = [];
  processResult: any = [];
  contactSituation: any = [];
  contactResult11: any = [];
  contactResult22: any = [];
  contactResult3: any = [];
  //机构树模态框
  treeOrgName: string = '';
  first: number = 0;//分页控制
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  dealDisplay: boolean = false;//处理
  dealInValue: any;//处理
  changeInValue: any; //转分配
  changeDisplay: boolean = false;//转分配
  changeDisplay2: boolean = false;//批量分配
  customerQuery: CustomerQuery = new CustomerQuery();//客户查询
  personName: string = '';//员工
  //选择员工
  userInValue: any;
  contactTrackFile: any;
  contactUserId: string;
  user: any;
  user2: any;
  chooseData: any = [];
  selectedAll: any = [];
  checkAll: boolean = false;
  checkBox: any = [];
  custContactTrackBean: CustContactTrackBean = new CustContactTrackBean();
  postList: any;
  userTreeDisplay: boolean = false;
  orgTreeDisplay: boolean = false;
  contactThemeList: any;
  contactTheme: any = [];
  contactWay: any;
  contactResult: any;
  currentStatusId: any;
  custSource: any;
  code: any;//获取所有码值
  ch: any = CALENDAR_CN;
  // 任务处理时间
  contactTimeStart: Date = null; // 任务处理开始时间
  contactTimeEnd: Date = null; // 任务处理结束时间
  // 任务发布时间
  deployTimeStart: Date = null; // 任务发布开始时间
  deployTimeEnd: Date = null; // 任务发布结束时间
  // 计划联系时间
  planContactTimeStart: Date = null; // 计划联系开始时间
  planContactTimeEnd: Date = null; // 计划联系结束时间
  msgs: Message[] = [];
  tablelist: any = [];
  totalNum: number;
  addUpHeader: any;//模态框标题
  display: boolean = false;
  addDisplay: boolean = false;
  peopleListDisplay: boolean = false;
  peopleListInValue: any;
  ThemeMarkingType: any;
  ThemeRiskType: any;
  custSourceList: any = [];
  custSourceList1: any = [];

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
    public route: ActivatedRoute,
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
  ) {

    this.codeValues();
    this.contactTheme = this.code['ContactTheme'];
    this.contactWay = this.code['ContactWays'];
    this.contactResult = this.code['ContactResultTrack'];
    this.currentStatusId = this.code['CurrentStatusId'];
    this.custSource = this.code['PlanSource'];

    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.user.userId) {
      this.contactUserId = this.user.userId
    }
    this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.queryOrgPeople();
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  //初始化
  ngOnInit() {

    if (this.route.snapshot.params['contactTheme']) {
      this.custContactTrackBean.contactTheme = this.route.snapshot.params['contactTheme'];
    }
    if (this.route.snapshot.params['contactStrategyId']) {
      this.custContactTrackBean.contactStrategyId = this.route.snapshot.params['contactStrategyId'];
    }
    if (this.route.snapshot.params['currentStatusId']) {
      this.custContactTrackBean.currentStatusId = this.route.snapshot.params['currentStatusId'];
    }
    this.custContactTrackBean.pageSize = 10;
    this.custContactTrackBean.pageNum = 1;
    this.queryOnit();
  }

  queryOrgPeople() {
    let param = {
      pageNum: 1,
      pageSize: 100000000,
      extParam: 'custContact',
    }

    let ob = this.commonHttpService.queryPostById(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.postInfoList) {
          this.postList = this.commfunc.listToLabelvalue(data.postInfoList, 'postName', 'postId');
          this.postList.unshift({ label: "请选择", value: '' });
          this.postList.push({ label: "主联系人", value: '0' });
          this.custContactTrackBean.receiveType = '';
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
      this.commfunc.clearSession('cusConditionId')
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.commfunc.clearSession('cusConditionId')
    })
  }

  // 初始查询-未处理的客户
  queryOnit() {
    this.doClearCheck();
    this.custContactTrackBean.pageSize = 10;
    this.custContactTrackBean.pageNum = 1;
    this.first = 0;
    this.custContactTrackBean.custSource = '1'; // 计划来源为上级分配
    this.custContactTrackBean.currentStatusId = '1'; // 当前状态为未处理的客户
    if (this.route.snapshot.params['currentStatusId']) {
      this.custContactTrackBean.currentStatusId = this.route.snapshot.params['currentStatusId'];
    }
    this.custContactTrackBean.receiveType = '';
    this.httpService.queryCustContactArrangement(this.custContactTrackBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.customerContactStrategyList;
        // for (let i in this.tablelist) {
        //   let a = this.tablelist[i];
        //   let b = a.ownerUser;
        //   let transfer = this.tablelist[i].ownerUser;
        //   let aa = transfer.split(',');
        //   if (aa.length > 1) {
        //     this.tablelist[i].ownerUser = aa[0] + '...'
        //   }
        // }
        this.totalNum = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 点击查询按钮
  querySecle() {
    this.custContactTrackBean.pageSize = 10;
    this.custContactTrackBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  //查询
  doQuery() {
    this.doClearCheck();
    if (this.custContactTrackBean.receiveType != undefined) {
      if (this.custContactTrackBean.receiveType == '') {
        this.custContactTrackBean.receiveTypeName = undefined;
      } else {
        for (let i of this.postList) {
          if (i.value == this.custContactTrackBean.receiveType) {
            this.custContactTrackBean.receiveTypeName = i.label;
            continue
          }
        }
      }
    }

    this.custContactTrackBean.contactTimeStart = this.commfunc.transDateN(this.contactTimeStart);
    this.custContactTrackBean.contactTimeEnd = this.commfunc.transDateN(this.contactTimeEnd);
    this.custContactTrackBean.deployTimeStart = this.commfunc.transDateN(this.deployTimeStart);
    this.custContactTrackBean.deployTimeEnd = this.commfunc.transDateN(this.deployTimeEnd);
    this.custContactTrackBean.planContactTimeStart = this.commfunc.transDateN(this.planContactTimeStart);
    this.custContactTrackBean.planContactTimeEnd = this.commfunc.transDateN(this.planContactTimeEnd);

    if (this.custContactTrackBean.contactTimeStart == '' || this.custContactTrackBean.contactTimeStart == null) {
      this.custContactTrackBean.contactTimeStart = undefined;
    }
    if (this.custContactTrackBean.contactTimeEnd == '' || this.custContactTrackBean.contactTimeEnd == null) {
      this.custContactTrackBean.contactTimeEnd = undefined;
    }
    if (this.custContactTrackBean.deployTimeStart == '' || this.custContactTrackBean.deployTimeStart == null) {
      this.custContactTrackBean.deployTimeStart = undefined;
    }

    if (this.custContactTrackBean.deployTimeEnd == '' || this.custContactTrackBean.deployTimeEnd == null) {
      this.custContactTrackBean.deployTimeEnd = undefined;
    }
    if (this.custContactTrackBean.planContactTimeStart == '' || this.custContactTrackBean.planContactTimeStart == null) {
      this.custContactTrackBean.planContactTimeStart = undefined;
    }
    if (this.custContactTrackBean.planContactTimeEnd == '' || this.custContactTrackBean.planContactTimeEnd == null) {
      this.custContactTrackBean.planContactTimeEnd = undefined;
    }

    this.httpService.queryCustContactArrangement(this.custContactTrackBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.customerContactStrategyList;
        this.totalNum = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 新增联系计划
  doAdd() {
    this.dealDisplay = false;
    this.changeDisplay = false;
    this.peopleListDisplay = false;
    this.addUpHeader = '新增';
    this.addDisplay = true;
    this.display = true;
  }

  // 关闭新增窗口
  addCall() {
    this.addDisplay = false;
    this.display = false;
    this.querySecle();
  }

  // 进度跟踪-导出
  doExport() {
    this.custContactTrackBean['userName'] = this.user2['userName'];
    let param = this.custContactTrackBean;
    this.httpService.exportCustomerContactArrangement(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let aa = data.fileName;
        window.open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(aa) + "&fileUrl=" + data.fileUrl)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //重置
  reset() {
    this.custContactTrackBean = new CustContactTrackBean();
    this.custContactTrackBean.pageSize = 10;
    this.custContactTrackBean.pageNum = 1;
    this.first = 0;
    this.contactTimeStart = null;
    this.contactTimeEnd = null;
    this.deployTimeStart = null;
    this.deployTimeEnd = null;
    this.planContactTimeStart = null;
    this.planContactTimeEnd = null;
  }

  // 点击-处理
  dealTask(data) {
    let param = data;
    this.dealInValue = param;
    this.addUpHeader = '处理';

    this.changeDisplay = false;
    this.peopleListDisplay = false;
    this.addDisplay = false;

    this.dealDisplay = true;
    this.display = true;
  }

  // 处理-关闭模态框
  dealCall(event) {
    this.dealDisplay = false;
    this.display = false;
    this.querySecle();
  }

  // 点击-转分配
  changeOwnPeople(col) {
    let carsParam: any = [];
    let arr = this.chooseData;
    carsParam.push({
      contactId: col.contactId,
    })
    let param = {
      custContactList: carsParam
    };
    this.changeInValue = param;
    this.addUpHeader = '转分配';

    this.peopleListDisplay = false;
    this.addDisplay = false;
    this.dealDisplay = false;

    this.changeDisplay = true;
    this.display = true;
  }

  // 转分配回调
  changeCall(event) {
    this.changeDisplay = false;
    this.display = false;
    this.chooseData = [];
    this.querySecle();
  }
  // 分配回调
  changeCall2(event) {
    this.changeDisplay2 = false;
    this.display = false;
    this.chooseData = [];
    this.querySecle();
  }

  // 选择机构/选择员工
  openTree(param) {
    if (param == 'org') {
      this.orgTreeDisplay = !this.orgTreeDisplay;
    } else {
      this.userTreeDisplay = !this.userTreeDisplay;
    }
  }

  // 关闭-机构模态框
  contacttreeCall(param) {
    this.orgTreeDisplay = false;
    this.custContactTrackBean.ownerOrg = param.orgId;
    this.custContactTrackBean.ownerOrgName = param.orgName;
  }

  // 关闭-员工模态框
  workerCall(item) {
    this.userTreeDisplay = false;
    if (item.length > 0) {
      this.custContactTrackBean.ownerUser = item[0].tellerId;
      this.custContactTrackBean.ownerUserName = item[0].tellerName;
    }
  }

  // 分页
  paginate(event) {
    this.custContactTrackBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.custContactTrackBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.custContactTrackBean.pageSize;
    this.doQuery();
  }

  // 按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

  // 开始、结束日期判断
  showTime(param1, param2) {
    if (param1 == 'deploy') {
      if (this.deployTimeStart && this.deployTimeEnd) {
        if (this.deployTimeStart > this.deployTimeEnd) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
          setTimeout(() => {
            if (param2 == "start") {
              this.deployTimeStart = null;
            } else {
              this.deployTimeEnd = null;
            }
          })
        }
      }
    }

    if (param1 == 'plan') {
      if (this.planContactTimeStart && this.planContactTimeEnd) {
        if (this.planContactTimeStart > this.planContactTimeEnd) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
          setTimeout(() => {
            if (param2 == "start") {
              this.planContactTimeStart = null;
            } else {
              this.planContactTimeEnd = null;
            }
          })
        }
      }
    }

    if (param1 == 'contact') {
      if (this.contactTimeStart && this.contactTimeEnd) {
        if (this.contactTimeStart > this.contactTimeEnd) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
          setTimeout(() => {
            if (param2 == "start") {
              this.contactTimeStart = null;
            } else {
              this.contactTimeEnd = null;
            }
          })
        }
      }
    }
  }

  //全选按钮
  checkAllClick() {
    this.chooseData = [];
    if (this.tablelist) {
      for (let i = 0; i < this.tablelist.length; i++) {
        if (this.checkAll == true) {
          this.tablelist[i].checkBox = true;
          if ((this.tablelist[i].custSource == '1' || this.tablelist[i].custSource == '2') &&
            (this.tablelist[i].currentStatusId == '1') && (this.tablelist[i].ownerUser != null &&
              this.tablelist[i].ownerUser.indexOf(this.contactUserId) != -1) && (this.tablelist[i].transferNum == 0) &&
            this.permissionCheck('SID00005_P004_P002_P212')) {
            this.chooseData.push(this.tablelist[i]);
          }
        }
        else {
          this.chooseData = [];
          this.tablelist[i].checkBox = false;
        }
      }
    }
  }
  //全选按钮2
  checkAllClick2() {
    this.chooseData = [];
    if (this.tablelist) {
      for (let i = 0; i < this.tablelist.length; i++) {
        if (this.checkAll == true) {
          this.tablelist[i].checkBox = true;
          if ((this.tablelist[i].custSource == '1' || this.tablelist[i].custSource == '2') &&
            (this.tablelist[i].currentStatusId == '1' || this.tablelist[i].currentStatusId == '3') &&
            this.permissionCheck('SID00005_P004_P002_P213')) {
            this.chooseData.push(this.tablelist[i]);
          }
        }
        else {
          this.chooseData = [];
          this.tablelist[i].checkBox = false;
        }
      }
    }
  }
  //单选按钮
  checkBoxClick(i, data) {
    if (data.checkBox) {
      this.chooseData.push(data)
    } else {
      this.chooseData.forEach((item, i) => {
        if (item == data) {
          this.chooseData.splice(i, 1)
        }
      });
    }
    if (this.chooseData.length == this.tablelist.length) {
      this.checkAll = true;
    } else {
      this.checkAll = false;
    }
  }

  // 批量转分配
  batchTranfer() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请至少选择一条记录' });
    } else {
      let carsParam: any = [];
      let arr = this.chooseData;
      arr.forEach((element, i) => {
        carsParam.push({
          contactId: element.contactId,
        })
      });
      let param = {
        custContactList: carsParam
      };
      this.changeInValue = param;

      this.addUpHeader = '转分配';
      this.dealDisplay = false;
      this.addDisplay = false;
      this.peopleListDisplay = false;
      this.changeDisplay2 = false;

      this.changeDisplay = true;
      this.display = true;
    }
  }
  // 批量分配
  batchDistribution() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请至少选择一条记录' });
    } else {
      let carsParam: any = [];
      let arr = this.chooseData;
      arr.forEach((element, i) => {
        carsParam.push({
          contactId: element.contactId,
        })
      });
      let param = {
        custContactList: carsParam
      };
      this.changeInValue = param;

      this.addUpHeader = '批量分配';
      this.dealDisplay = false;
      this.addDisplay = false;
      this.peopleListDisplay = false;
      this.changeDisplay = false;

      this.changeDisplay2 = true;
      this.display = true;
    }
  }
  //清除全选勾选框+单选框
  doClearCheck() {
    this.chooseData = [];
    this.checkAll = false;
  }

  // 联系人工号列表
  OwnerUserDetail(data) {
    let param = data.contactId;
    this.peopleListInValue = param;
    this.addUpHeader = '详情';

    this.dealDisplay = false;
    this.addDisplay = false;
    this.changeDisplay = false;

    this.peopleListDisplay = true;
    this.display = true;
  }

  // 关闭联系人工号列表
  peopleListCall() {
    this.peopleListDisplay = false;
    this.display = false;
    this.querySecle();
  }

  // 删除客户联系任务
  delete(item) {
    this.confirmationService.confirm({
      message: '删除后将不可恢复，确认删除该记录?',
      accept: () => {
        let param = {
          contactId: item.contactId,// 聯絡ID
        };
        this.httpService.deleteCustContactArrangementByMyself(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.querySecle();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  // 手动输入日期
  handInputDate(param1, param2) {
    this.showTime(param1, param2);
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }

}
