/**
 * @author fanhualing 2018-4-18 查询客户联系策略
 */
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { CustContactBean } from './bean/custContactStrategy.bean';
import { CustContactTrackBean } from './../contact-track/bean/custContactTrack.bean';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service'
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ActivatedRoute, Router } from '@angular/router';
import * as _ from 'lodash';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";

@Component({
  selector: 'app-policy-settings',
  templateUrl: './policy-settings.component.html',
  styleUrls: ['./policy-settings.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class PolicySettingsComponent implements OnInit {

  contactThemeList: any;
  postList: any;
  arr: any = [];
  contactTheme: any = [];
  contactOrg: any = [];
  taskAcceptType: any = [];
  strategyStatus: any;
  custContactBean: CustContactBean = new CustContactBean();
  custContactTrackBean: CustContactTrackBean = new CustContactTrackBean();
  businessType: any = [];
  bSubType: any = [];
  contactFrequency: any = [];
  targetCustomerInputType: any = [];
  businessSubType: any = [];
  stopId: any;
  contactDate: any;
  ch: any = CALENDAR_CN;
  msgs: Message[] = [];
  selectedAll: any = [];
  checkAll: boolean = false;
  checkBox: any = [];
  display: boolean = false;
  addDisplay: boolean = false; // 新增
  updateDisplay: boolean = false; // 修改
  userTreeDisplay: boolean = false;
  inValue: any;
  listInValue: any;
  keyValue: any;
  lookinValue: any;
  addUpHeader: any;
  kindDo: string;
  list: any;
  tablelist: any;
  first: number = 0;
  totalNum: number = 0;
  orgTreeDisplay: boolean = false;
  contactOrgTreeDisplay2: boolean = false;
  contacttreeOrgName: string = '';
  createdOrgTreeDisplay: boolean = false;
  createdtreeOrgName: string = '';
  tabMesg: any = tableMessage;
  showFlag: boolean = true;
  planContactTimeFrom: Date = null;
  planContactTimeTo: Date = null;
  showFlag1: boolean = true;
  isImport: boolean;
  addLabel: string;
  userInValue: any;
  createUser: string;
  createdUserName: string;
  deployTimeFrom: Date = null;
  deployTimeTo: Date = null;
  code: any;
  user: any;
  userId: string;
  // 选人
  waitSelUserList: any;
  outVal: any;
  aaaa: boolean = false;
  queryStatus: any = [];
  strategyId: string = '';
  // 码值
  custSource: any;
  contactWay: any;

  ThemeLevelOne: any;
  ThemeMarkingType: any;
  ThemeRiskType: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public confirmationService: ConfirmationService,
    public route: ActivatedRoute,
    public router: Router,
    public commonHttpService: CommonHttpService,
    public ce: ChangeDetectorRef
  ) {
    //调用码值
    this.codeValues();
    this.custSource = this.code['CustSource'];
    this.strategyStatus = this.code['StrategyStatus'];
    this.contactWay = this.code['ContactWays'];
    this.contactTheme = this.code['ContactTheme'];

    this.strategyStatus.forEach(element => {
      if (element.label != '导入中') {
        this.queryStatus.push(element);
      }
    });

    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
    this.queryOrgPeople();
  }

  //初始化
  ngOnInit() {
    let aa = this.commfunc.getSessionDataCH('cusConditionId');
    if (this.commfunc.getSessionDataCH('cusConditionId')) {
      this.inValue = '';
      this.keyValue = this.arr;
      this.addUpHeader = '新增';
      this.updateDisplay = false;
      this.addDisplay = true;
      this.display = true;
      this.kindDo = 'add'
      this.list = '';
      this.isImport = false;
    }
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.custContactBean.pageSize = 10;
    this.custContactBean.pageNum = 1;
    this.querySecle();
  }

  // 获取码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  // 初始化表格
  querySecle() {
    this.custContactBean.pageSize = 10;
    this.custContactBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  // 查询联系策略列表
  doQuery() {
    // 根据任务接收类型receiveType，查询任务接收类型名称receiveTypeName
    if (this.custContactBean.receiveType != undefined) {
      if (this.custContactBean.receiveType == '') {
        this.custContactBean.receiveTypeName = undefined;
      } else {
        for (let i of this.postList) {
          if (i.value == this.custContactBean.receiveType) {
            this.custContactBean.receiveTypeName = i.label;
            continue;
          }
        }
      }
    }
    // 日期处理并筛选，如果日期为空，则不传给后台
    this.custContactBean.deployTimeFrom = this.commfunc.transDateN(this.deployTimeFrom);
    this.custContactBean.deployTimeTo = this.commfunc.transDateN(this.deployTimeTo);
    this.custContactBean.planContactTimeFrom = this.commfunc.transDateN(this.planContactTimeFrom);
    this.custContactBean.planContactTimeTo = this.commfunc.transDateN(this.planContactTimeTo);
    if (this.custContactBean.deployTimeFrom == '' || this.custContactBean.deployTimeFrom == null) {
      this.custContactBean.deployTimeFrom = undefined;
    }
    if (this.custContactBean.deployTimeTo == '' || this.custContactBean.deployTimeTo == null) {
      this.custContactBean.deployTimeTo = undefined;
    }
    if (this.custContactBean.planContactTimeFrom == '' || this.custContactBean.planContactTimeFrom == null) {
      this.custContactBean.planContactTimeFrom = undefined;
    }
    if (this.custContactBean.planContactTimeTo == '' || this.custContactBean.planContactTimeTo == null) {
      this.custContactBean.planContactTimeTo = undefined;
    }
    // 查询服务
    let ob = this.httpService.queryCustomerContactStrategyList(this.custContactBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.contactStrategyList;
        this.totalNum = data.totalCount;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 调用公共接口，查询任务接收类型列表
  queryOrgPeople() {
    let param = {
      pageNum: 1,
      pageSize: 100000000,
      extParam: 'custContact'
    }
    let ob = this.commonHttpService.queryPostById(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.postInfoList) {
          this.postList = this.commfunc.listToLabelvalue(data.postInfoList, 'postName', 'postId');
          this.postList.unshift({ label: "请选择", value: '' });
          this.postList.push({ label: "主联系人", value: '0' });
          this.custContactBean.receiveType = '';
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
      this.commfunc.clearSession('cusConditionId');
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.commfunc.clearSession('cusConditionId');
    })
  }

  //修改
  modify(data) {
    this.addUpHeader = '修改';
    this.addDisplay = false; // 新增
    this.display = true;
    this.updateDisplay = true; // 修改
    this.inValue = data;
  }

  //修改-关闭模态框
  updateCall() {
    this.updateDisplay = false;
    this.display = false;
    this.querySecle();
  }

  // 中止联系策略
  stop(col) {
    this.confirmationService.confirm({
      message: '中止当前联系主题后不可恢复，确认中止该记录?',
      accept: () => {
        let param = {
          contactStrategyId: col.contactStrategyId,// 联系策略标识
        };
        this.httpService.sunspensionCustomerContactStrategy(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.querySecle();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  // 删除客户联系策略
  delete(item) {
    this.confirmationService.confirm({
      message: '删除后将不可恢复，确认删除该记录?',
      accept: () => {
        let param = {
          contactStrategyId: item.contactStrategyId,// 联系策略ID
        };
        this.httpService.deleteCustomerContactStrategy(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.querySecle();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  // 发布
  publish(col) {
    
    this.strategyId = col.contactStrategyId;
    // 客户联系审批-是否选人
    this.waitSelUserList = this.commfunc.query('LXCLLC', {});
    if( !this.waitSelUserList.erro){
        this.outVal = this.waitSelUserList.waitSelUserList;
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let a = commonHeader.extParam.specifyNextOpers;
        if (this.waitSelUserList.display && (a == undefined || a == '' || a == null)) {
          this.aaaa = true;
        }else{
          this.breakcast();
        }
    }else{
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
    }
  }

  outDisplayCall(event) {
    this.aaaa = false;
  }

  // 选人之后回调
  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.breakcast();
  }

  // 发布到审批阶段
  breakcast() {
    this.confirmationService.confirm({
      message: '请确认策略发布',
      accept: () => {
        let param = {
          contactStrategyId: this.strategyId,
        };
        this.httpService.approveCustomerContactStrategy(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
              delete s.extParam.specifyNextOpers
              let commonHeader = s;
              this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.aaaa = false;
            let a9 = data2 => { this.querySecle(); }
            setTimeout(a9, 1000);
          } else {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
              delete s.extParam.specifyNextOpers
              let commonHeader = s;
              this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
          if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
          }
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })

      },
      reject: () => {
      }
    });
  }

  // 再次名单导入
  importAgain(col) {
    this.add(col);
  }

  // 再次高级搜索导入
  advancedAgain(col){
    let param = col;
    this.addUpHeader = '新增';
    this.updateDisplay = false;
    this.display = true; // 模态框
    this.addDisplay = true; // 新增

    if (param != '') {
      let paramIn = {
        contactStrategyId:col.contactStrategyId,
        type:'advance',
      }
      this.inValue = paramIn;
    } else {
      this.inValue = undefined;
    }
  }

  //重置
  reset() {
    this.custContactBean = new CustContactBean();
    this.custContactBean.receiveType = '';
    this.createUser = undefined;
    this.createdUserName = undefined;
    this.deployTimeFrom = null;
    this.deployTimeTo = null;
    this.planContactTimeFrom = null;
    this.planContactTimeTo = null;
    this.custContactBean.pageSize = 10;
    this.custContactBean.pageNum = 1;
    this.first = 0;
  }

  //客户名单-关闭模态框
  addCallList() {
    this.display = false;
    this.querySecle();
  }

  // 点击联系主题--进入进度跟踪页面
  goTrck(data) {
    let param = {
      contactTheme: data.contactTheme,
      contactStrategyId: data.contactStrategyId,
      currentStatusId:this.custContactTrackBean.currentStatusId='请选择',
    }
    this.router.navigate(['pages/tzb/custom/custom-marketing/cus-contact/custom-contact-policy/contact-progress-track', param]);
  }

  // 点击编号--进入联系策略名单页面
  Jump(data) {
    let param = {
      contactStrategyId: data.contactStrategyId,
    }
    this.router.navigate(['pages/tzb/custom/custom-marketing/cus-contact/custom-contact-policy/contact-list', param]);
  }

  // 开始、结束日期判断
  showTime(param1, param2) {
    if (param1 == 'deploy') {
      if (this.deployTimeFrom && this.deployTimeTo) {
        if (this.deployTimeFrom > this.deployTimeTo) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
          setTimeout(() => {
            if (param2 == "start") {
              this.deployTimeFrom = null;
            } else {
              this.deployTimeTo = null;
            }
          })
        }
      }
    }
    if (param1 == 'plan') {
      if (this.planContactTimeFrom && this.planContactTimeTo) {
        if (this.planContactTimeFrom > this.planContactTimeTo) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
          setTimeout(() => {
            if (param2 == "start") {
              this.planContactTimeFrom = null;
            } else {
              this.planContactTimeTo = null;
            }
          })
        }
      }
    }
  }

  //分页
  paginate(event) {
    this.custContactBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.custContactBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.custContactBean.pageSize;
    this.doQuery();
  }

  // 选择机构/选择员工
  openTree(param) {
    if (param == 'org') {
      this.orgTreeDisplay = !this.orgTreeDisplay;
    } else {
      this.userTreeDisplay = !this.userTreeDisplay;
    }
  }

  // 关闭-机构模态框
  contacttreeCall(param) {
    this.orgTreeDisplay = false;
    this.custContactBean.createOrg = param.orgId;
    this.custContactBean.createdOrgName = param.orgName;
  }

  // 关闭-员工模态框
  workerCall(item) {
    this.userTreeDisplay = false;
    if (item.length > 0) {
      this.custContactBean.createUser = item[0].tellerId;
      this.custContactBean.createdUserName = item[0].tellerName;
    }
  }

  // 新增客户联系策略(名单导入)
  add(param) {
    let arr = param;
    this.addUpHeader = '新增';
    this.updateDisplay = false;
    this.display = true; // 模态框
    this.addDisplay = true; // 新增

    if (param != '') {
      let paramIn = {
        contactStrategyId:arr.contactStrategyId,
        type:'import',
      }
      this.inValue = paramIn;
    } else {
      this.inValue = undefined;
    }
  }

  // 新增客户联系策略-关闭模态框
  addCall(event) {
    this.addDisplay = false;
    this.display = false;
    this.querySecle();
    this.addLabel = '';
  }

  // 手动输入日期
  handInputDate(param1, param2) {
    this.showTime(param1, param2);
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}

