/**
 * @author fanhualing 2018-4-20 修改联系策略
 */
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CustContactStrategyBean } from '../bean/custContactStrategy.bean';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service'
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { PolicyBeanUpdate } from "app/pages/tzb/custom/custom-marketing/custom-contact/policy-settings/update-policy/bean/update-policy.bean";
import { ActivatedRoute, Router } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { CommonHttpService } from "../../../../../common/http/common.http.service";

@Component({
  selector: 'app-update-policy',
  templateUrl: './update-policy.component.html',
  styleUrls: ['./update-policy.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class UpdatePolicyComponent implements OnInit {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';

  ch: any = CALENDAR_CN;
  msgs: any = [];
  policyBeanUpdate: PolicyBeanUpdate = new PolicyBeanUpdate();
  contactTheme: any;
  contactWay: any;
  custSource: any;
  planContactTime: any;
  contactOrg: any = [];
  receiveType: any = [];
  fileName: string;
  filePath: string;
  fileList: any = [];
  files: any = [];
  fileUrl: string;
  tablelistSuccess: any;
  tablelistFail: any;
  successTotalNum: number;
  failTotalNum: number;
  totalNum: number;
  totalImportList: any = [];
  importTrue: boolean = false;
  btnText: string;
  arr: any;
  cars: any;
  pageNum: number;
  pageSize: number;
  pageList: any = [];
  totalList: any = [];
  fileId: string = '';
  userform: FormGroup;
  //机构树模态框
  orgTreeDisplay: boolean = false;
  treeOrgName: string = '';
  custScope: string = '';
  Property: boolean = false;
  //获取所有码值
  code: any;
  otherTheme: boolean = false;
  postInfoList: any;
  postList: any;
  lenRemark: boolean = false;
  commentsRequire: boolean = false;
  isCanUp: boolean = true;
  timeError: boolean = false;
  //定义当前时间
  planContactTimeStart: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public routeInfo: ActivatedRoute,
    public router: Router,
    public fb: FormBuilder,
    public commonHttpService: CommonHttpService,
  ) {
    this.codeValues();
    this.contactTheme = this.code['ContactTheme'];
    this.contactWay = this.code['ContactWays'];
    this.custSource = this.code['CustSource'];

    this.userform = fb.group({
      contactTheme: ['', Validators.required],
      contactWay: ['', Validators.required],
      receiveType: ['', Validators.required],
      planContactTime: ['', Validators.required],
      custSource: ['', Validators.required],
      comments: ['', Validators.required],
      remark1: [''],
    });

    this.planContactTimeStart = new Date(this.getMinMaxDate('min'));
    // 查公共接口-获取任务接收类型
    this.queryOrgPeople();
  }

  //初始
  ngOnInit() {
    for (let i in this.inValue) {
      this.policyBeanUpdate[i] = this.inValue[i];
    }
    this.planContactTime = new Date(this.inValue.planContactTime.split('-').join('/') + ' 23:59:59');
    if (this.policyBeanUpdate.contactTheme == '06') {
      this.otherTheme = true;
    }
    if (this.inValue.statusId == '5' || this.inValue.statusId == '8') {
      this.isCanUp = true;
    } else {
      this.isCanUp = false;
    }
  }

  // 获取码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  // 任务接收类型
  queryOrgPeople() {
    let param = {
      pageNum: 1,
      pageSize: 100000000,
      extParam: 'custContact',
    }
    let ob = this.commonHttpService.queryPostById(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.postInfoList) {
          this.postInfoList = data.postInfoList;
          this.postList = this.commfunc.listToLabelvalue(data.postInfoList, 'postName', 'postId');
          this.postList.unshift({ label: "请选择", value: '' });
          this.postList.push({ label: "主联系人", value: '0' });
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 更改主题，当主题为其他时，显示说明字段
  changeTheme() {
    this.policyBeanUpdate.remark1 = '';
    if (this.policyBeanUpdate.contactTheme == '06') {
      this.otherTheme = true;
    } else {
      this.otherTheme = false;
    }
  }

  // 手动输入日期
  handInputDate(param) {
    let date = new Date();
    if (param == 'planContactTime') {
      if (this.planContactTime != null && this.planContactTime != '') {
        if (this.commfunc.transDateN(this.planContactTime) < this.commfunc.transDateN(date)) {
          this.timeError = true;
        }else{
          this.timeError = false;
        }
      }else{
        this.timeError = false;
      }
    }
  }

  // 获取今天最大最小日期
  getMinMaxDate(param) {
    let time = new Date();
    let year = time.getFullYear();
    let month = time.getMonth() + 1 + '';
    let data = time.getDate() + '';
    if (month.length < 2) {
      month = '0' + month;
    }
    if (data.length < 2) {
      data = "0" + data
    }
    let todayTime;
    if (param == 'min') {
      todayTime = year + "/" + month + "/" + data + ' 00:00:00';
    }
    if (param == 'max') {
      todayTime = year + "/" + month + "/" + data + ' 23:59:59';
    }
    return todayTime;
  }

  // 联系策略修改
  doUpdate() {
    // 校验其他主题-说明字段
    if (this.policyBeanUpdate.contactTheme == '06') {
      if (this.policyBeanUpdate.remark1 != undefined && this.policyBeanUpdate.remark1 != null && this.policyBeanUpdate.remark1 != '') {
        if (this.policyBeanUpdate.remark1.length > 10) {
          this.lenRemark = true;
        } else {
          this.lenRemark = false;
        }
      }
    }
    if (this.planContactTime != undefined && this.planContactTime != null) {
      this.policyBeanUpdate.planContactTime = this.commfunc.transDateN(this.planContactTime);
      if(this.policyBeanUpdate.planContactTime < this.commfunc.transDateN(this.planContactTimeStart)){
        this.timeError = true;
      }else{
        this.timeError = false;
      }
    }
   
    // 校验页面数据
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    

    // 如果说明长度过长
    if (this.lenRemark == true) {
      return
    }

    // 如果备注长度过长
    if (this.policyBeanUpdate.comments.length > 200) {
      this.commentsRequire = true;
      return
    } else {
      this.commentsRequire = false;
    }

    // 根据任务接收类型receiveType，查询任务接收类型名称receiveTypeName
    for (let i of this.postList) {
      if (i.value == this.policyBeanUpdate.receiveType) {
        this.policyBeanUpdate.receiveTypeName = i.label;
        if (this.postInfoList && this.postInfoList.length > 0) {
          this.postInfoList.map(element => {
            if (i.value == element.postId) {
              this.policyBeanUpdate.remark2 = element.extParam;
            }
          });
        }
        continue;
      }
    }

    this.policyBeanUpdate.planContactTime = this.commfunc.transDateN(this.planContactTime);
    let ob = this.httpService.updateCustomerContactStrategy(this.policyBeanUpdate);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

}
