export class CustContactStrategyBean {
    isChoose: string                   // 主联系人联系
    businessType: string               // 业务类型
    businessSubType: string            // 联系主题
    contactFrequency: string           // 联络频度
    contactWay: string                 // 联系方式
    contactOrgType: string             // 联系机构类型
    contactOrg: string                 // 联系机构
    contactEmpType: string             // 联系人类型
    contactEmp: string                 // 联系人
    targetCustomerInputType: string    // 目标客户输入类型
    status: string                     // 状态
    endDtFrom: string                  // 结束日期起
    endDtTo: string                    // 结束日期止
    lxzt: string
    pageNum: number                    // 当前页数
    pageSize: number                   // 每页条数
}

export class CustContactBean {

    custSource: string; //客户来源(N)
    contactTheme: string; //联络主题(N)
    contactWay: string; //联络方式(N)
    receiveType: string; //高级搜索-任务接收类型(N)
    deployTimeFrom: string; //任务发布时间-起(N) yyyy-MM-dd
    deployTimeTo: string; //任务发布时间-止(N) yyyy-MM-dd
    planContactTimeFrom: string; //计划联系时间-起(N) yyyy-MM-dd
    planContactTimeTo: string; //计划联系时间-止(N) yyyy-MM-dd
    statusId: string; //状态(N)
    createOrg: string; //创建机构(Y)
    createUser: string; //创建人(Y)
    pageNum: number; //页码(Y)
    pageSize: number; //每页显示记录条数(Y)
    receiveTypeName: string; //任务接收类型名称
    createdUserName:string; //创建人名称
    createdOrgName:string; //创建机构名称
}	
