import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'

@Component({
  selector: 'app-custom-contact-policy',
  templateUrl: './custom-contact-policy.component.html',
  styleUrls: ['./custom-contact-policy.component.scss'],
  providers: [Commfunc]
})
export class CustomContactPolicyComponent implements OnInit {
  show: number;
  nav1: boolean = true;
  nav2: boolean = false;
  nav3: boolean = false;
  totalNum: number = 0;
  flag: boolean = false;
  constructor(
    public commfunc: Commfunc,
    public router: Router, ) { }

  ngOnInit() {
    let stu_1 = this.commfunc.permissionCheck('SID00005_P004_P001');
    let stu_2 = this.commfunc.permissionCheck('SID00005_P004_P002');
    let stu_3 = this.commfunc.permissionCheck('SID00005_P004_P003');
    if (stu_1) {
      this.show = 1;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-contact/custom-contact-policy/policy-settings'])
    } else if (stu_2) {
      this.show = 2;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-contact/custom-contact-policy/contact-progress-track'])
    } else if (stu_3) {
      this.show = 3;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-contact/custom-contact-policy/contact-track-summary'])
    }
  }
  apply(num) {
    this.show = num;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
