import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/components/common/api';
import { SelectItem } from 'primeng/primeng';
import { QueryPlanBean } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-plan/query-plan/bean/query-plan.bean";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-query-plan',
  templateUrl: './query-plan.component.html',
  styleUrls: ['./query-plan.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
/**
 * 查询客户联系计划
 */
export class QueryPlanComponent implements OnInit {

  queryPlanBean: QueryPlanBean = new QueryPlanBean();
  lxzt: any = [];
  contactStatus: any = [];
  contactWay: any = [];
  CALENDAR_CN: any = [];
  msgs: Message[] = [];
  tablelist: any = [];
  totalNum: number;
  temDate: any = new Date();
  claimCreatedDate: any;
  claimBeginDate: any;
  claimEndDate: any;
  display: boolean = false;
  ExamDisplay: boolean;
  addDisplay: boolean;
  updateDisplay: boolean = false;
  inValue: any;
  keyValue: any;
  addUpHeader: any;
  kindDo: string;
  arr: any;
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public confirmationService: ConfirmationService
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.lxzt = this.code['Lxzt'];
    this.contactStatus = this.code['ContactStatus'];
    this.contactWay = this.code['ContactWays'];

    this.CALENDAR_CN = {
      "firstDayOfWeek": 0,
      "dayNames": ["周日", "周一", "周二", "周三", "周四", "周五", "周六"],
      "dayNamesShort": ["星期天", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"],
      "dayNamesMin": ["日", "一", "二", "三", "四", "五", "六"],
      "monthNames": ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"],
      "monthNamesShort": ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"]
    }
  }

  //初始化
  ngOnInit() {
    this.queryPlanBean.pageSize = 10;
    this.queryPlanBean.pageNum = 1;
  
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //日期转换
  showCreateData() {
    this.queryPlanBean.claimCreatedDate = this.commfunc.transDate(this.claimCreatedDate);
  }
  showData() {
    this.temDate = this.claimBeginDate;
    this.queryPlanBean.claimBeginDate = this.commfunc.transDate(this.claimBeginDate);
    this.queryPlanBean.claimEndDate = this.commfunc.transDate(this.claimEndDate);
  }

  //重置
  reset() {
    this.queryPlanBean.contactTheme = '';
    this.queryPlanBean.contactStatus = '';
    this.queryPlanBean.contactWay = '';
    this.claimBeginDate = '';
    this.claimEndDate = '';
    this.claimCreatedDate = '';
  }

  //查询
  doQuery() {
    this.httpService.queryCustContactProgram(this.queryPlanBean).subscribe(data => {
      this.tablelist = data.pageList;
      this.totalNum = data.totalNum;
      if (data.returnCode.code == 'success') {
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //修改
  modify(data) {
    this.inValue = data;
    this.keyValue = this.arr;
    this.addUpHeader = '修改';
    this.display = true;
    this.addDisplay = false;
    this.updateDisplay = true;
    this.kindDo = 'modify'
  }

  //修改-关闭模态框
  updateCall() {
    this.updateDisplay = false;
    this.display = false;
  }

  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        let param = { "taskId": item.taskId };
        this.httpService.deleteCustContactProgram(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.queryPlanBean.pageNum = 1;
            this.doQuery();
          } else {
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  //分页
  paginate(event) {
    this.queryPlanBean.pageSize = event.rows * 1;
    this.queryPlanBean.pageNum = event.page + 1;
    this.doQuery();
  }
}
