import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/components/common/api';
import { SelectItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { QueryPlanBean } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-plan/bean/query-plan.bean";
import * as _ from 'lodash';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { DealTaskBean } from "../contact-track/deal-task/bean/deal-task.bean";//查询任务引入
@Component({
  selector: 'app-contact-plan',
  templateUrl: './contact-plan.component.html',
  styleUrls: ['./contact-plan.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
/**
 * 客户联系计划
 */
export class ContactPlanComponent implements OnInit {
  dealTaskBean: DealTaskBean = new DealTaskBean();
  queryPlanBean: QueryPlanBean = new QueryPlanBean();
  lxzt: any = [];
  contactStatus: any = [];
  contactWay: any = [];
  ch: any = CALENDAR_CN;
  msgs: Message[] = [];
  tablelist: any = [];
  totalNum: number = 0;
  //开始日期temDateStart
  temDateStart: any = new Date();
  claimCreatedDate: any;
  claimBeginDate: Date = null;
  claimEndDate: Date = null;
  display: boolean = false;
  ExamDisplay: boolean;
  addDisplay: boolean;
  updateDisplay: boolean = false;
  dealDisplay: boolean = false;
  inValue: any;
  dealInValue: any;
  keyValue: any;
  addUpHeader: any;
  kindDo: string;
  arr: any;
  first: number = 0;//分页控制
  //机构树模态框
  orgTreeDisplay: boolean = false;//模态框是否展示
  treeOrgName: string = '';
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public confirmationService: ConfirmationService
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.lxzt = this.code['Lxzt'];
    this.contactStatus = this.code['ContactStatus'];
    this.contactWay = this.code['ContactWays'];
  }
  //初始化
  ngOnInit() {
    this.queryPlanBean.pageSize = 10;
    this.queryPlanBean.pageNum = 1;
    this.tablelist = [{ contactsNo: '1222' }]
  }
  querySecle() {
    this.queryPlanBean.pageSize = 10;
    this.queryPlanBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询
  doQuery() {
    this.queryPlanBean.claimBeginDate = this.commfunc.transDate(this.claimBeginDate);
    this.queryPlanBean.claimEndDate = this.commfunc.transDate(this.claimEndDate);
    this.httpService.queryCustContactProgram(this.queryPlanBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.pageList;
        this.totalNum = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }
  //新增
  add() {
    this.keyValue = this.arr;
    this.addUpHeader = '新增';
    this.display = true;
    this.addDisplay = true;
    this.dealDisplay = false;
    this.updateDisplay = false;
    this.kindDo = 'add'
  }
  //新增-关闭模态框
  addCall(event) {
    this.addDisplay = false;
    this.display = false;
    this.querySecle();
  }
  //修改
  modify(data) {
    this.inValue = data;
    this.keyValue = this.arr;
    this.addUpHeader = '修改';
    this.display = true;
    this.addDisplay = false;
    this.dealDisplay = false;
    this.updateDisplay = true;
    this.kindDo = 'modify'
  }
  //修改-关闭模态框
  updateCall() {
    this.updateDisplay = false;
    this.display = false;
    this.querySecle();
  }
  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        let param = { "taskId": item.taskId };
        this.httpService.deleteCustContactProgram(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            // this.msgs = [];
            // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.querySecle();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }
  //处理
  dealTask(col) {
    this.dealInValue = col;
    this.addUpHeader = '处理';
    this.addDisplay = false;
    this.updateDisplay = false;
    this.display = true;
    this.dealDisplay = true;

  }
  //处理-关闭模态框
  dealCall() {
    this.dealDisplay = false;
    this.display = false;
    this.querySecle();
  }

  //分页
  paginate(event) {
    this.queryPlanBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.queryPlanBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.queryPlanBean.pageSize;
    this.doQuery();
  }
  //重置
  reset() {
    this.queryPlanBean = new QueryPlanBean();
    this.claimBeginDate = null;
    this.claimEndDate = null;
    this.claimCreatedDate = '';
    this.querySecleReset();
  }
  //页码重置
  querySecleReset() {
    this.queryPlanBean.pageSize = 10;
    this.queryPlanBean.pageNum = 1;
    this.first = 0;
  }
  //计划联系时间：开始日期
  showDataStart() {
    if (this.claimBeginDate && this.claimEndDate) {
      if (this.claimBeginDate > this.claimEndDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.claimBeginDate = null;
        })
      }
    }
  }
  //计划联系时间：结束日期
  showDataEnd() {
    if (this.claimBeginDate && this.claimEndDate) {
      if (this.claimBeginDate > this.claimEndDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.claimEndDate = null;
        })
      }
    }
  }
  //联系计划新建时间：
  showDataCreate() {
    this.queryPlanBean.claimCreatedDate = this.commfunc.transDate(this.claimCreatedDate);
  }
  //机构
  openOrgTreeClick() {
    this.orgTreeDisplay = !this.orgTreeDisplay;
  }
  // 机构树回调
  treeCall(param) {
    this.orgTreeDisplay = false;
    this.queryPlanBean.contactOrg = param.orgName;
    this.queryPlanBean.contactOrgId = param.orgId;
  }
}
