import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service'
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ContactTrackPeopleListBean } from 'app/pages/tzb/custom/custom-marketing/custom-contact/contact-track/contact-track-people-list/bean/ContactTrackPeopleList.bean';
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
  selector: 'app-contact-track-people-list',
  templateUrl: './contact-track-people-list.component.html',
  styleUrls: ['./contact-track-people-list.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]

})
export class ContactTrackPeopleListComponent implements OnInit {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';

  tabMesg: any = tableMessage;
  msgs: any = [];
  btnText: string;
  adopt: string;
  noadopt: string;
  arr: any;
  cars: any;
  userform: FormGroup;
  contactTrackPeopleListBean: any = new ContactTrackPeopleListBean();
  tablelist: any;
  totalNum: number = 0;
  first: number = 0;

  constructor(
    public httpService: CusOperationHttpService,
    public fb: FormBuilder,
    public confirmationService: ConfirmationService,
  ) {

  }

  ngOnInit() {
    if (this.inValue) {
      this.contactTrackPeopleListBean.contactId = this.inValue;
    }
    this.contactTrackPeopleListBean.pageNum = 1;
    this.contactTrackPeopleListBean.pageSize = 10;
    this.first = 0;
    this.doQuery();
  }

  // 查询名单列表
  doQuery() {
    this.httpService.queryCustWorkOwnerUser(this.contactTrackPeopleListBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.userList;
        this.totalNum = data.totalNum;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //分页
  paginate(event) {
    this.contactTrackPeopleListBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.contactTrackPeopleListBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.contactTrackPeopleListBean.pageSize;
    this.doQuery();
  }
}
