/**
 * @author fanhualing 2018-4-17 查询客户联系名单
 */
import { Component, OnInit } from '@angular/core';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service'
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from "@angular/router";
import { ContactListBean } from 'app/pages/tzb/custom/custom-marketing/custom-contact/contact-list/bean/ContactList.bean';
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
  selector: 'app-contact-list',
  templateUrl: './contact-list.component.html',
  styleUrls: ['./contact-list.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService],
})

export class ContactListComponent implements OnInit {

  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  msgs: Message[] = [];
  totalNum: number = 0;
  first: number = 0;//分页控制
  tablelist: any = [];
  display: boolean = false;
  upDisplay: boolean = false;
  processStatus: any;
  contactListBean: any = new ContactListBean();
  inValue: any;
  queryStatus: any = [];
  peopleDisplay: boolean = false;
  custSource: any;
  addUpHeader: any;
  code: any;
  strategyId: string;
  // 选人
  waitSelUserList: any;
  outVal: any;
  aaaa: boolean = false;
  // choosePeople: boolean = false;
  showBatchCheck: any;
  showBatchDelete: any;
  showDeploy: any;
  showTodo: any;
  showBatchCheck1: boolean = false;
  showBatchDelete1: boolean = false;
  showDeploy1: boolean = false;
  showTodo1: boolean = false;
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public confirmationService: ConfirmationService,
    public router: Router,
    public route: ActivatedRoute,
  ) {
    //调用码值
    this.codeValues();
    this.processStatus = this.code['ProcessStatus'];

    if (this.route.snapshot.params['contactStrategyId']) {
      this.contactListBean.contactStrategyId = this.route.snapshot.params['contactStrategyId'];
    }
    this.contactListBean.pageSize = 10;
    this.contactListBean.pageNum = 1;
    this.first = 0;
  }

  //初始化
  ngOnInit() {
    this.querySecle();
  }

  // 获取码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  // 查询之前初始化
  querySecle() {
    this.contactListBean.pageSize = 10;
    this.contactListBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  // 查询
  doQuery() {
    // 联系策略
    this.httpService.queryCustConArrangeMent(this.contactListBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.resultList;
        this.totalNum = data.total;
        this.custSource = data.custSource;
        if (data.showBatchCheck == '1') {
          this.showBatchCheck1 = true;
        } else {
          this.showBatchCheck1 = false;
        };
        if (data.showBatchDelete == '1') {
          this.showBatchDelete1 = true;
        } else {
          this.showBatchDelete1 = false;
        };
        if (data.showDeploy == '1') {
          this.showDeploy1 = true;
        } else {
          this.showDeploy1 = false;
        };
        if (data.showTodo == '1') {
          this.showTodo1 = true;
        } else {
          this.showTodo1 = false;
        };
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 修改
  modify(data) {
    let param = data;
    this.inValue = param;
    this.inValue['contactStrategyId'] = this.contactListBean.contactStrategyId;
    this.peopleDisplay = false;
    this.addUpHeader = '修改';
    this.upDisplay = true;
    this.display = true;
  }

  // 修改关闭
  modifyCall() {
    this.display = false;
    this.upDisplay = false;
    this.peopleDisplay = false;
    this.querySecle();
  }

  // 联系人工号列表
  upOwnerUser(data) {
    let param = data.contactId;
    this.inValue = param;
    this.upDisplay = false;
    this.addUpHeader = '详情';
    this.peopleDisplay = true;
    this.display = true;
  }

  // 联系人工号列表-关闭
  peopleCall() {
    this.display = false;
    this.upDisplay = false;
    this.peopleDisplay = false;
    this.querySecle();
  }

  // 异常名单处理-删除
  delete(item) {
    this.confirmationService.confirm({
      message: '删除后将不可恢复，确认删除该记录?',
      accept: () => {
        let param = {
          contactId: item.contactId,
        };
        this.httpService.deleteFailCustConArrangement(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            // 删除成功
            this.querySecle();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }
  //批量校验
  batchCheck() {
    this.confirmationService.confirm({
      message: '只对状态为导入中的数据进行批量校验！',
      accept: () => {
        let param = {
          contactStrategyId: this.contactListBean.contactStrategyId,
        };
        this.httpService.batchCheckCustContactArrangement(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            //校验成功
            this.querySecle();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  // 批量删除
  batchDeal() {

    let param = {
      contactStrategyId: this.contactListBean.contactStrategyId,
    }
    this.httpService.batchDeleteCustomerContactArrangement(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        //删除成功重新查询一次
        this.querySecle();
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 发布
  publish() {
    let param = {
      contactStrategyId: this.contactListBean.contactStrategyId,
    }
    this.httpService.checkApproveCustomerContactStrategy(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.strategyId = this.contactListBean.contactStrategyId;
        this.waitSelUserList = this.commfunc.query('LXCLLC', {});
        if (!this.waitSelUserList.erro) {
          this.outVal = this.waitSelUserList.waitSelUserList;
          let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
          let a = commonHeader.extParam.specifyNextOpers;
          if (this.waitSelUserList.display && (a == undefined || a == '' || a == null)) {
            this.aaaa = true;
          } else {
            this.breakcast();
          }
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
        }

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  outDisplayCall(event) {
    this.aaaa = false;
  }
  //失败待补发
  failed() {
    let param = {
      contactStrategyId: this.contactListBean.contactStrategyId,

    }
    this.httpService.batchDeployCustomerContactWorkeffort(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: "调用服务成功" })

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 选人之后回调
  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.breakcast();
  }

  // 发布到审批阶段
  breakcast() {
    this.confirmationService.confirm({
      message: '请确认策略发布',
      accept: () => {
        let param = {
          contactStrategyId: this.strategyId,
        };
        this.httpService.approveCustomerContactStrategy(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
              delete s.extParam.specifyNextOpers
              let commonHeader = s;
              this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.aaaa = false;
            let a9 = data2 => { this.querySecle(); }
            setTimeout(a9, 1000);
          } else {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
              delete s.extParam.specifyNextOpers
              let commonHeader = s;
              this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
          if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
          }
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })

      },
      reject: () => {
      }
    });
  }

  // 重置
  reset() {
    this.contactListBean.custId = undefined;
    this.contactListBean.custName = undefined;
    this.contactListBean.processStatus = undefined;
    this.contactListBean.pageSize = 10;
    this.contactListBean.pageNum = 1;
    this.first = 0;
  }

  // 分页
  paginate(event) {
    this.contactListBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.contactListBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.contactListBean.pageSize;
    this.doQuery();
  }
  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}
